import React, { useContext, useEffect, useState } from 'react'
import { Formik } from 'formik'
import { FormContext } from '../../pages/SignUp.tsx'
import checkEmailVerificationCode from '../../useCases/checkEmailVerificationCode.ts'
import { toast } from 'react-toastify'

const CheckEmail = () => {
  const { activeStepIndex, setActiveStepIndex, formData, setFormData } = useContext(FormContext)
  const [timer, setTimer] = useState<number>(600)

  useEffect(() => {
    const interval = setInterval(() => setTimer(timer - 1), 100)

    return () => clearInterval(interval)
  }, [])

  return (
    <>
      <Formik
        initialValues={{
          code: '',
        }}
        validate={(values) => {
          const errors = {}

          if (!values.code) {
            errors.code = ' .'
          } else if (values.code.length !== 6) {
            errors.code = ' 6.'
          }

          return errors
        }}
        onSubmit={async (values) => {
          const data = { ...formData, ...values }
          setFormData(data)
          try {
            await checkEmailVerificationCode({ email: formData.email, code: values.code })
            setActiveStepIndex(activeStepIndex + 1)
          } catch (e) {
            toast('   .')
          }
        }}
      >
        {({ values, errors, touched, handleChange, handleBlur, handleSubmit, isSubmitting }) => (
          <form className="flex flex-col justify-center items-center" onSubmit={handleSubmit}>
            <div className="flex flex-col gap-2 items-start mb-2 w-[100%]">
              <label className="font-medium text-gray-900">Email</label>
              <input name="email" disabled value={formData.email} className="rounded-md border-2 p-2" />
            </div>
            <div className="flex flex-col gap-2 items-start mb-2">
              <label className="font-medium text-gray-900"></label>
              <div className="flex items-center gap-1">
                <input
                  name="code"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.code}
                  className="rounded-md border-2 p-2"
                  placeholder="123456"
                  maxLength={6}
                />
                <p className="text-blue-500">
                  {Math.floor(timer / 60)}:{timer % 60}
                </p>

                <button
                  disabled={values.code.length !== 6}
                  className="rounded-md btn btn-primary text-white ml-4"
                  type="submit"
                >
                    
                </button>
              </div>
              <p className="text-red-500 mt-2">{errors.code && touched.code && errors.code}</p>
            </div>
          </form>
        )}
      </Formik>
      <button
        className="btn  btn-secondary"
        onClick={() => {
          setActiveStepIndex(activeStepIndex - 1)
        }}
      >
        
      </button>
    </>
  )
}
export default CheckEmail
