import React, { useEffect } from 'react'
import useSiteRegisterDialog from './useSiteRegisterDialog.ts'
import { mutate } from 'swr'
import { BASE_URL } from '../common/api.js'

const SiteRegisterDialog = ({ id, registerCheckNumber }: { id?: number; registerCheckNumber: number }) => {
  const {
    metaTag,
    name,
    url,
    description,
    setName,
    setUrl,
    copyMetaTag,
    setDescription,
    onClickNextButton,
    onClickCheckButton,
    step,
    setStep,
    onCloseModal,
    isDisabledNextBtn,
    errorMessage
  } = useSiteRegisterDialog({ id: id ? id : undefined, registerCheckNumber })



  if (!metaTag || step === 1)
    return (
      <dialog id="site_register_dialog" className="modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold"> </h3>
          <form action="">
            <label className="w-full form-control">
              <div className="label">
                <span className="label-text"> </span>
              </div>
              <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="quack run"
                className="w-full input input-bordered"
              />
            </label>
            <label className="w-full form-control">
              <div className="label">
                <span className="label-text">URL</span>
              </div>
              <input
                type="text"
                value={url}
                onChange={(e) => setUrl(e.target.value)}
                placeholder="https://quack.run"
                className="w-full input input-bordered"
              />
            </label>
            <label className="w-full form-control">
              <div className="label">
                <span className="label-text"></span>
              </div>
              <textarea
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                className="textarea textarea-bordered"
                placeholder="    . "
              ></textarea>
            </label>
          </form>

          <div className="modal-action">
            <button
              className="btn"
              onClick={() => {
                onCloseModal()
              }}
            >
              
            </button>
            <button
              disabled={isDisabledNextBtn}
              onClick={() => onClickNextButton()}
              className="btn btn-primary"
              type="button"
            >
               
            </button>
          </div>
        </div>
      </dialog>
    )

  if (step === 2) {
    return (
      <dialog id="site_register_dialog" className="modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold">  </h3>
          <p className="text-sm">{url}</p>
          <p className="text-xs py-4">{`       <body>   <head>   `}</p>
          <div className="flex pt-4 gap-3 w-full">
            <input
              className="bg-gray-100 p-2 rounded-sm w-full"
              disabled
              value={`<meta name="quack-run-service-verification" content="${metaTag}">`}
            ></input>
            <button className="btn btn-outline" onClick={() => copyMetaTag()}>
              
            </button>
          </div>
          <div className="flex pt-4 gap-3 justify-end">
            <button
              className="btn"
              onClick={() => {
                onCloseModal()
                mutate(`${BASE_URL}/sites`)
              }}
            >
              
            </button>
            <button onClick={() => onClickCheckButton()} className="btn btn-primary" type="button">
              
            </button>
          </div>
        </div>
      </dialog>
    )
  }

  if (step === 3) {
    return (
      <dialog id="site_register_dialog" className="modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold">  .</h3>

          <p>      </p>

          <button
            className="btn"
            onClick={() => {
              onCloseModal()
              mutate(`${BASE_URL}/sites`)
            }}
          >
            
          </button>
        </div>
      </dialog>
    )
  }
  if (step === 4) {
    return (
      <dialog id="site_register_dialog" className="modal">
        <div className="modal-box">
          <h3 className="text-lg font-bold">   .</h3>

          <p>{errorMessage}</p>

          <div className="modal-action">
            <button
              className="btn"
              onClick={() => {
                onCloseModal()
                mutate(`${BASE_URL}/sites`)
              }}
            >
              
            </button>
            <button
              className="btn btn-secondary"
              onClick={() => {
                setStep(2)
              }}
            >
              
            </button>
          </div>
        </div>
      </dialog>
    )
  }
}
export default SiteRegisterDialog
